import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './style.css';
import { Link } from 'react-router-dom';
import logo from '../../images/logo.png';

class Header extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      toggleMenu: false,
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick() {
    this.setState({
      toggleMenu: !this.state.toggleMenu
    })
  }

  render() {
    const { toggleMenu } = this.state;
    return (
      <header className="header-asserts">
        <div className="header-content d-flex flex-row text-center">
          <Link to="/">
            <img src={logo} className="logo-img" alt="logo" />
          </Link>
          <ul className={`menu-list text-center d-flex ${!toggleMenu ? 'toggle-menu' : ''}`}>
            <li>
              <Link to="/">Home</Link>
            </li>
            <li>
              <Link to="/projects">Projects</Link>
            </li>
            <li>
              <Link to="/experiences">Experiences</Link>
            </li>
            <li>
              <Link to="/competencies">Competencies</Link>
            </li>
            <li>
              <Link to="/contact">Contact</Link>
            </li>
          </ul>
          <ul className="d-flex menu-list-icon justify-content-center">
            <li>
              <svg
                className={`ham ham6 desktop-hide ${toggleMenu ? 'active' : ''}`}
                viewBox="0 0 100 100"
                width="100"
                onClick={this.handleClick}>
                <path
                  className="line top"
                  d="m 30,33 h 40 c 13.100415,0 14.380204,31.80258 6.899646,33.421777 -24.612039,5.327373 9.016154,-52.337577 -12.75751,-30.563913 l -28.284272,28.284272"
                />
                <path
                  className="line middle"
                  d="m 70,50 c 0,0 -32.213436,0 -40,0 -7.786564,0 -6.428571,-4.640244 -6.428571,-8.571429 0,-5.895471 6.073743,-11.783399 12.286435,-5.570707 6.212692,6.212692 28.284272,28.284272 28.284272,28.284272"
                />
                <path
                  className="line bottom"
                  d="m 69.575405,67.073826 h -40 c -13.100415,0 -14.380204,-31.80258 -6.899646,-33.421777 24.612039,-5.327373 -9.016154,52.337577 12.75751,30.563913 l 28.284272,-28.284272"
                />
              </svg>
            </li>
          </ul>
        </div>
      </header>
    );
  }
}

export default Header;
