import React from 'react';
import Header from '../../components/Header';
import profile from '../../images/profile-pic.JPG';
import '../../css/global.css';
import './style.css';
import { DiGithubBadge } from 'react-icons/di';
import { AiFillLinkedin } from 'react-icons/ai';
import { AiOutlineWhatsApp } from 'react-icons/ai';
import { AiOutlineFacebook } from 'react-icons/ai';
import Footer from '../../components/Footer';

class Contact extends React.Component {
  render() {
    return (
      <div className="father-container d-flex flex-column">
        <Header main={true} />
        <main className="d-flex flex-column align-items-left main">
          <p className="title">Rodrigo Freitas Batista</p>
          <div className="d-flex">
            <div className="texts">
              <div className="d-inline subtitle">{`Contact me!`}</div>
              <br />
              <div className="d-inline phrase0">{`<rfreitasbatista@gmail.com />`}</div>
              <br />
              <div className="d-inline phrase0">{`<(11)99968-3629 />`}</div>
              <br />
              <div className="contact-badges">
                <ul className="d-flex menu-list-icon-contact">
                  <li>
                    <a
                      href="https://github.com/rfreitasbatista"
                      target="_blank"
                      rel="noopener noreferrer">
                      <DiGithubBadge />
                    </a>
                  </li>
                  <li>
                    <a
                      href="https://www.linkedin.com/in/rfreitasbatista/"
                      target="_blank"
                      rel="noopener noreferrer">
                      <AiFillLinkedin />
                    </a>
                  </li>
                  <li>
                    <a
                      href="https://api.whatsapp.com/send?phone=5511999683629"
                      target="_blank"
                      rel="noopener noreferrer">
                      <AiOutlineWhatsApp />
                    </a>
                  </li>
                  <li>
                    <a
                      href="https://www.facebook.com/rfreitasbatista/"
                      target="_blank"
                      rel="noopener noreferrer">
                      <AiOutlineFacebook />
                    </a>
                  </li>
                </ul>
              </div>
            </div>
            <img className="profile-pic" src={profile} alt="profile-pic" />
          </div>
        </main>
        <Footer />
      </div>
    );
  }
}

export default Contact;
