import React from 'react';
import Project from '../../components/Project';
import 'bootstrap/dist/css/bootstrap.min.css';
import './style.css';
import * as writer from '../../data/data';
import Header from '../../components/Header';
import advanced from '../../images/competencies/advanced.png';
import Footer from '../../components/Footer';

class Projects extends React.Component {
  render() {
    return (
      <div className="d-flex flex-column project-container">
        <Header />
        <div>
          <h4 className="titles">
            {'<Main Projects>'}
          </h4>
          <div className="d-flex justify-content-left container-fluid projects flex-wrap">
            {writer.projetos
              .filter((projeto) => projeto.main)
              .map((each) => (
                <Project
                  key={each.id}
                  src={each.colored}
                  name={each.name}
                  description={each.description}
                  id={each.id}
                  features={each.features}
                  link={each.link}
                  underC={each.underC}
                />
              ))}
          </div>
          <h4 className="titles">
            {'<All Projects>'}
          </h4>
          <div className="d-flex justify-content-left container-fluid projects flex-wrap">
            {writer.projetos
              .filter((each) => !each.underC)
              .map((projeto) => (
                <Project
                  key={projeto.id}
                  src={projeto.colored}
                  name={projeto.name}
                  description={projeto.description}
                  id={projeto.id}
                  features={projeto.features}
                  link={projeto.link}
                  underC={projeto.underC}
                />
              ))}
          </div>
          <h4 className="titles">
            {'<Under Construction>'}
          </h4>
          <div className="d-flex justify-content-left container-fluid projects flex-wrap">
            {writer.projetos
              .filter((each) => each.underC)
              .map((projeto) => (
                <Project
                  key={projeto.id}
                  src={projeto.colored}
                  name={projeto.name}
                  description={projeto.description}
                  id={projeto.id}
                  features={projeto.features}
                  link={projeto.link}
                  underC={projeto.underC}
                />
              ))}
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

export default Projects;
