import React from 'react';
import Header from '../../components/Header';
import Bootstrap from '../../images/competencies/Bootstrap.png';
import CSSi from '../../images/competencies/CSS3.png';
import git from '../../images/competencies/git.png';
import HTML from '../../images/competencies/HTML5.png';
import javascript from '../../images/competencies/javascript.png';
import jest from '../../images/competencies/jest.png';
import jquery from '../../images/competencies/jquery.png';
import linux from '../../images/competencies/linux.png';
import reactjs from '../../images/competencies/reactjs.png';
import redux from '../../images/competencies/redux.png';
import rtl from '../../images/competencies/rtl.png';
import VSCode from '../../images/competencies/VSCode.png';
import 'bootstrap/dist/css/bootstrap.min.css';
import './style.css';
import Footer from '../../components/Footer';

class Competencies extends React.Component {
  render() {
    return (
      <div className="d-flex flex-column max">
        <Header />
        <div className="column-competencies d-flex flex-wrap">
          <p className="color-advise text-center">
            The competence color shows the knowledge level
          </p>
          <div className="text-center badge-container languages">
            <h3 className="titles">{'<languages />'}</h3>
            <div className="d-flex justify-content-center">
              <div className="comp-badges">
                <img src={HTML} width={70} alt="badge" />
                <p>HTML</p>
              </div>
              <div className="comp-badges">
                <img src={CSSi} width={70} alt="badge" />
                <p>CSS</p>
              </div>
              <div className="comp-badges">
                <img src={javascript} width={70} alt="badge" />
                <p>Javascript</p>
              </div>
            </div>
          </div>
          <div className="text-center badge-container libraries">
            <h3 className="titles">{'<libraries />'}</h3>
            <div className="d-flex justify-content-center">
              <div className="comp-badges">
                <img src={jquery} width={70} alt="badge" />
                <p>JQuery</p>
              </div>
              <div className="comp-badges">
                <img src={reactjs} width={70} alt="badge" />
                <p>ReactJS</p>
              </div>
              <div className="comp-badges">
                <img src={redux} width={70} alt="badge" />
                <p>Redux</p>
              </div>
              <div className="comp-badges">
                <img src={rtl} width={70} alt="badge" />
                <p>React Testing Library</p>
              </div>
            </div>
          </div>
          <div className="text-center badge-container frameworks">
            <h3 className="titles">{'<frameworks />'}</h3>
            <div className="d-flex justify-content-center">
              <div className="comp-badges">
                <img src={Bootstrap} width={85} alt="badge" />
                <p>Bootstrap</p>
              </div>
              <div className="comp-badges">
                <img src={jest} width={85} alt="badge" />
                <p>Jest</p>
              </div>
            </div>
          </div>
          <div className="badge-container d-flex justify-content-center combined">
            <div className="text-center">
              <h4 className="titles">{'<OS />'}</h4>
              <div className="comp-badges">
                <img src={linux} width={70} alt="badge" />
                <p>Linux</p>
              </div>
            </div>
            <div className="text-center">
              <h4 className="titles">{'<Version Control'}</h4>
              <div className="comp-badges">
                <img src={git} width={70} alt="badge" />
                <p>Git</p>
              </div>
            </div>
            <div className="text-center">
              <h4 className="titles">{'<Text Editor />'}</h4>
              <div className="comp-badges">
                <img src={VSCode} width={70} alt="badge" />
                <p>Visual Studio Code</p>
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

export default Competencies;
